import React from 'react';
import {
  StyledSection,
  TextContainer,
  SectionTitle,
  SectionDescription,
  SectionNumber,
} from './information-section.styles';
import { SectionImage } from '../../about/section/section.styles';

interface InformationSectionProps {
  title: string;
  description: string;
  image: 'workspace' | 'content';
  sectionNumber: number;
  id?: string;
  textPosition?: 'left' | 'right';
}

export const InformationSection = ({
  title,
  description,
  image,
  sectionNumber,
  id,
  textPosition,
}: InformationSectionProps) => {
  const containerRef = React.createRef<HTMLDivElement>();
  const [containerHeight, setContainerHeight] = React.useState(0);
  const [isActive, setActive] = React.useState(false);

  const handleResize = React.useCallback(() => {
    setContainerHeight(containerRef.current?.offsetHeight);
  }, [containerRef]);

  const handleScroll = React.useCallback(() => {
    if (
      !isActive &&
      window.pageYOffset - sectionNumber * (containerHeight / 2) >
        containerRef.current?.getBoundingClientRect().top
    ) {
      setActive(true);
    }
  }, [containerRef, isActive, containerHeight, sectionNumber]);

  React.useEffect(() => {
    if (containerRef.current) {
      setContainerHeight(containerRef.current.offsetHeight);
    }
  }, [containerRef]);

  React.useEffect(() => {
    window.addEventListener('resize', handleResize);

    return () => window.removeEventListener('resize', handleResize);
  }, [handleResize]);

  React.useEffect(() => {
    window.addEventListener('scroll', handleScroll);

    return () => window.removeEventListener('scroll', handleScroll);
  }, [handleScroll]);

  return (
    <StyledSection ref={containerRef} className="styled-section" id={id}>
      <TextContainer>
        <SectionTitle>{title}</SectionTitle>
        <SectionDescription>{description}</SectionDescription>
      </TextContainer>
      <SectionNumber
        active={isActive}
        containerHeight={containerHeight}
        className="section-number"
      >
        {sectionNumber}
      </SectionNumber>
      <SectionImage
        alt="Section"
        src={`${process.env.ASSET_PREFIX}/how-to-contribute/${image}.svg`}
      />
    </StyledSection>
  );
};
